import React, {useState} from "react";
import MintForm from "./MintForm";
import { isWhitelisted, isCodeAvailable } from "utils/contract";
import { useSelector } from 'react-redux';
import { connectState, connectedAddress } from "app/reducers/walletSlice";
import { useWeb3React } from '@web3-react/core';

function MintingNFTPage(props) {
    const [isWhite, setIsWhite] = useState(false);

    const {active, account} = useWeb3React();
    const [referrerID, setReferrerID] =useState(props.match.params.referrerID);

    React.useEffect(async() => {
        let isValid = await isCodeAvailable(referrerID);
        if (isValid) setReferrerID("1");

        if (active) {
            let flag = await isWhitelisted(account);
            setIsWhite(flag);
            // console.log(flag, "iswhite");
            if (flag) setReferrerID("1");
        } else setIsWhite(false);
    }, [active, account]);

    return (
        <>
            <MintForm isWhite={isWhite} isRef={true} referrerID={referrerID} />
        </>
    );
}

export default MintingNFTPage;