import React from "react";
import { getSubReferral, getReferralDeadline, getUSDInToken } from "../../utils/contract";
import { calDayHoursFromTimestamp, calDateFromSeconds, getTimestamp } from "utils/util";
import ReferralTable from "./tables";
import AnalyticBoard from "./AnalyticBoard";
import { useWeb3React } from '@web3-react/core';

function ReferralDashBoard() {
    // const active = useSelector(connectState);
    // const account = useSelector(activeAddress);
    const {active, account} = useWeb3React();

    const [referrals, setReferrals] = React.useState([]);
    const [totalRewards, setTotalRewards] = React.useState(0);
    const [referralDeadline, setReferralDealine] = React.useState(0);
    const [referralNum, setReferralNum] = React.useState(0);
    const [tokenPrice, setTokenPrice] = React.useState(1);
    const [secondRefNum, setSecondRefNum] = React.useState(0);

    const loadReferrals = async (account) => {
        let _referrals = [];
        try {
            _referrals = await getSubReferral(account);
            if (referrals!=null)
                await postProcessReferrals(_referrals);
        } catch(error) {
            await postProcessReferrals([]);
        }
        
    }

    const postProcessReferrals = async (_referrals) => {
        let referrals_ = [];
        let secondRefCount = 0;
        let currentTimestamp = getTimestamp();
        for(let i=0; i < Math.floor(_referrals.length/7); i++) {
            let deadline = await calDayHoursFromTimestamp(_referrals[i*7+3], currentTimestamp, referralDeadline);
            let flag = 0;
            if (deadline === "-") flag = 1;
            referrals_.push({
                usercode: _referrals[i*7+1],
                address: "0x" + _referrals[i*7+2],
                creationTime: _referrals[i*7+3],
                rewardAmount: _referrals[i*7+4] / 1000000,
                isReferred: parseInt(_referrals[i*7+5]),
                secondRefTime: _referrals[i*7+6],
                isExpired: parseInt(_referrals[i*7+7]) + flag,
                deadline: deadline
            });
            if (_referrals[i*7+5] !== "0") secondRefCount += 1;
        }
        setReferrals(referrals_);
        getTotalRewards(referrals_);
        setReferralNum(referrals_.length);
        setSecondRefNum(secondRefCount);
    }

    const getTotalRewards = (referrals_) => {
        let totalRewards = 0;
        referrals_.forEach(referral => {
            totalRewards += referral.rewardAmount;
            if (referral.isReferred > 0)
                totalRewards += referral.rewardAmount;
        });
        setTotalRewards(totalRewards.toFixed(2));
    }

    const setReferralDeadline = async () => {
        let referralDeadline = await getReferralDeadline();
        setReferralDealine(referralDeadline);
    }

    const setBaseTokenPrice = async () => {
        let price = await getUSDInToken();
        setTokenPrice(price);
    }

    const refresh = () => {
        loadReferrals(account);
        setBaseTokenPrice();
    }
    const initialize = async () => {
        if (active) {
            loadReferrals(account);
        } else {
            setReferrals([]);
            setReferralNum(0);
            setTotalRewards(0.00);
        }
        setBaseTokenPrice();
        setReferralDeadline();
    };

    React.useLayoutEffect(() => {
        if (active) {
            const interval = setInterval(async () => {
                await initialize();
            }, 5000);
            
            return () => clearInterval(interval);
        }
    },  [active, account]);

    return (
        <>
            <div>
                <AnalyticBoard 
                    totalRewards={(totalRewards/10**12).toFixed(2)} 
                    referralNum={referralNum}
                    secondRefNum={secondRefNum}
                    deadline = {calDateFromSeconds(referralDeadline)}
                />
                <ReferralTable referrals={referrals} tokenPrice={tokenPrice} referralDeadline={referralDeadline} mb={3} />
            </div>
        </>
    );
}
export default ReferralDashBoard;